<template>
  <div>
    <div class="container">
      <!--上传插件与搜索-->
      <div class="handle-box">
        <!--上传插件-->
        <div>
          <el-upload
            class="upload-demo"
            action="/mydataharbor/plugin/uploadPlugin"
            :on-success="handleSuccess"
            :on-error="handleError"
            :show-file-list="showFile">
            <el-button size="small" type="primary">上传插件</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jar文件，且不超过500M</div>
          </el-upload>
        </div>
        <!--搜索-->
        <div>
          <el-input v-model="query.pluginId" placeholder="插件id" size="small"
                    clearable class="handle-input mr10" @clear="clear"></el-input>
          <el-input v-model="query.version" placeholder="插件版本" size="small"
                    clearable class="handle-input mr10" @clear="clear"></el-input>
          <el-input v-model="query.des" placeholder="插件描述" size="small"
                    clearable class="handle-input mr10" @clear="clear"></el-input>
          <el-button type="primary" icon="el-icon-search" @click="handleSearch" size="small" >搜索</el-button>
        </div>
      </div>
      <!--表格数据-->
      <el-table :data="tableData" border class="table" row-key="id" max-height="600"
                ref="multipleTable" header-cell-class-name="table-header">
        <el-table-column prop="id" label="ID" width="65" align="center" sortable></el-table-column>
        <el-table-column prop="pluginId" label="插件id" width="250"></el-table-column>
        <el-table-column prop="version" label="版本" width="60"></el-table-column>
        <el-table-column prop="fileName" label="文件名" width="440"></el-table-column>
        <el-table-column prop="pluginDescription" label="描述" width="160"></el-table-column>
        <!--<el-table-column prop="pluginStorePath" label="存储路径" width="250"></el-table-column>-->
        <el-table-column label="创建时间" width="144" sortable>
          <template slot-scope="scope">
            <span>{{ scope.row.createTime | FormatDate('yyyy-MM-dd HH:mm') }}</span>
          </template>
        </el-table-column>
        <el-table-column label="更新时间" width="144" sortable>
          <template slot-scope="scope">
            <span>{{ scope.row.updateTime | FormatDate('yyyy-MM-dd HH:mm') }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template #default="scope">
            <el-link :href="'/mydataharbor/plugin/downloadPlugin?pluginId=' + scope.row.pluginId + '&version=' + scope.row.version"
                     type="primary" icon="el-icon-download" target="_blank">下载</el-link>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination background layout="sizes, total, prev, pager, next" :current-page="query.pageNo"
                       :page-sizes="[5,10,20,50]" :page-size="query.pageSize" :total="pageTotal"
                       @size-change="handleSizeChange"
                       @current-change="handlePageChange"></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Plugin",
  data() {
    return {
      query: {
        pageNo: 1,
        pageSize: 10,
        pluginId: '',
        version: '',
        des: ''
      },
      tableData: [],
      pageTotal: 0,
      //是否显示上传进度
      showFile: true
    }
  },
  mounted() {
    this.initData();
  },
  methods: {
    initData() {
      console.log("loading info……");
      this.getRequest("mydataharbor/plugin/listPlugins", this.query).then(res=>{
        this.tableData = res.records;
        this.pageTotal = res.total;
        console.log(res);
      })
    },
    //搜索
    handleSearch() {
      this.initData();
    },
    clear() {
      this.initData();
    },
    //分页处理
    handlePageChange(pageNo) {
      this.query.pageNo = pageNo;
      this.initData();
    },
    handleSizeChange(size) {
      this.query.pageSize = size;
      this.initData();
    },
    //插件上传
    handleSuccess(response, fileList) {
      console.log("success");
      if (response.code == -1) {
        this.$message.error(response.msg);
        this.showFile = false;
      }

    },
    handleError(err, file, fileList) {
      console.log("error" + err);
      console.log(file);
      console.log(fileList);
    }
  }
}
</script>

<style scoped>
.handle-box {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.handle-input {
  width: 150px;
  display: inline-block;
}
.table {
  width: 100%;
  font-size: 14px;
}
.mr10 {
  margin-right: 10px;
}
</style>



