<template>
  <div>
    <!--提交任务-->
    <div style="margin-bottom: 10px">
      <el-button type="primary" size="small" @click="dialogFormVisible = true">提交任务</el-button>
      <el-button type="danger" size="small" @click="batchDelete()">批量删除</el-button>
    </div>
    <!--表格数据-->
    <el-table :data="dataShow" border class="table" width="100%" max-height="690"
              ref="multipleTable" header-cell-class-name="table-header"
              @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="taskId" label="任务ID" width="240" sortable></el-table-column>
      <el-table-column label="任务名" width="120" :show-overflow-tooltip="true">
        <template #default="scope">
          <span v-if="scope.row.taskName != undefined">{{ scope.row.taskName }}</span>
          <span v-else>null</span>
        </template>
      </el-table-column>
      <el-table-column label="状态" width="100">
        <template #default="scope">
          <el-tag :type=taskState[scope.row.taskState].color size="small">{{ taskState[scope.row.taskState].zh }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="pluginId" label="插件ID" width="250" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column prop="mydataharborCreatorClazz" label="创建器" width="150"
                       :show-overflow-tooltip="true"></el-table-column>
      <!--   <el-table-column prop="groupName" label="组名" width="100"></el-table-column>-->
      <el-table-column label="负载均衡" width="120">
        <template #default="scope">
          <el-tag v-if="scope.row.enableRebalance" size="small">true</el-tag>
          <el-tag v-else size="small" type="warning">false</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="totalNumberOfPipline" label="管道数" width="80"></el-table-column>
      <el-table-column label="创建时间" width="144" sortable>
        <template slot-scope="scope">
          <span>{{ scope.row.createTime | FormatDate('yyyy-MM-dd HH:mm') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="更新时间" width="144" sortable>
        <template slot-scope="scope">
          <span>{{ scope.row.lastUpdateTime | FormatDate('yyyy-MM-dd HH:mm') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="tags" width="120" :show-overflow-tooltip="true">
        <template #default="scope">
          <span v-if="scope.row.tags != undefined">{{ scope.row.tags }}</span>
          <span v-else>null</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" fixed="right" width="280">
        <template #default="scope">
          <el-button @click="taskDetail(scope.row)" type="text">详情</el-button>
          <el-button @click="taskUpdate(scope.row)" type="text" style="color: #67C23A">修改</el-button>
          <el-button @click="taskFork(scope.row)" type="text" style="color: #E6A23C">fork</el-button>
          <el-button @click="taskStates(scope.row)" type="text" style="color: #909399">状态</el-button>
          <el-button @click="taskDelete(scope.row)" type="text" style="color: #F56C6C">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--分页-->
    <div class="pagination">
      <el-pagination background layout="sizes, total, prev, pager, next" :current-page="currentPage"
                     :page-sizes="[5,10,20,50,100]" :page-size="pageSize" :total="pageTotal"
                     @size-change="handleSizeChange"
                     @current-change="handlePageChange"></el-pagination>
    </div>
    <!--提交/fork任务弹框-->
    <el-dialog :title="dialogName" :visible.sync="dialogFormVisible" width="80%">
      <el-form :model="form" label-position="left" label-width="70px">
        <el-row>
          <el-col :span="11">
            <el-form-item label="组名">
              <el-input v-model="form.groupName" autocomplete="off"
                        size="small" style="width: 80%" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="插件ID" prop="pluginId">
              <el-select v-model="form.pluginId" @change="selectPluginId" placeholder="请选择组件ID"
                         size="small" style="width: 100%">
                <el-option v-for="item in pluginInstallList" v-bind:key="item.id" :label="item.pluginId"
                           :value="item.pluginId"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="管道数">
              <el-input v-model="form.totalNumberOfPipline" autocomplete="off"
                        size="small" style="width: 80%"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="创建器">
              <el-select v-model="form.mydataharborCreatorClazz" placeholder="请选择CreatorClazz"
                         size="small" style="width: 100%">
                <el-option v-for="item in clazzList" v-bind:key="item.id" :label="item.type+(item.clazz)"
                           :value="item.clazz"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="11">
            <el-form-item label="任务名">
              <el-input v-model="form.taskName" placeholder="请输入任务名" autocomplete="off" size="small"
                        style="width: 80%"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="负载均衡">
              <el-select v-model="form.enableRebalance" placeholder="请选择是否负载均衡" size="small" style="width: 100%">
                <el-option label=true value=true></el-option>
                <el-option label=false value=false></el-option>
              </el-select>
            </el-form-item>
            <el-alert title="值为false，节点停机，任务将不会自动转移！" type="info" show-icon style="margin-top: -20px"></el-alert>
          </el-col>
        </el-row>
      </el-form>

      <el-collapse v-model="collapseActiveNames">
        <el-collapse-item title="settingContextConfig" name="settingConfig">
          <el-tabs v-model="defaultActiveModel">

            <el-tab-pane label="json" name="json">
              <vue-json-editor v-model="form.settingJsonConfig" :mode="'code'" lang="zh"></vue-json-editor>
            </el-tab-pane>

            <el-tab-pane label="参数说明" name="form">

              <el-tree :data="settingConfigJsonTreeData" :props="defaultProps" :default-expand-all=true
                       :render-content="renderTreeContent"></el-tree>

            </el-tab-pane>


          </el-tabs>
        </el-collapse-item>
        <el-collapse-item title="taskConfig" name="taskConfig">


          <el-tabs v-model="taskConfigDefaultActiveModel">

            <el-tab-pane label="json" name="json">
              <vue-json-editor v-model="form.configJson" :mode="'code'" lang="zh"></vue-json-editor>
            </el-tab-pane>

            <el-tab-pane label="参数说明" name="form">

              <el-tree :data="taskConfigJsonTreeData" :props="defaultProps" :default-expand-all=true
                       :render-content="renderTreeContent"></el-tree>

            </el-tab-pane>


          </el-tabs>


        </el-collapse-item>
        <el-collapse-item title="tagConfig" name="tagConfig">
          <vue-json-editor v-model="form.tags" :mode="'code'" lang="zh"></vue-json-editor>
        </el-collapse-item>

      </el-collapse>


      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>

    <!--任务详情-->
    <el-dialog title="任务详情" :visible.sync="taskDetailVisible" width="42%">
      <p>基本信息：</p>
      <el-table :data="taskDetailForm">
        <!--折叠-->
        <el-table-column type="expand">
          <template slot-scope="props">
            <p>任务分配信息：</p>
            <el-table :data="taskAssignedInfo">
              <!--折叠-->
              <el-table-column type="expand">
                <template slot-scope="props">
                  <p>管道信息：</p>
                  <p v-for="(value,key) in props.row.piplineStates">{{ key }}：{{ value }}</p>
                  <el-divider></el-divider>
                  <p>写总计：</p>
                  <p v-for="(value,key) in props.row.writeTotal">{{ key }}：{{ value }}</p>
                </template>
              </el-table-column>
              <el-table-column prop="nodeName" label="节点" width="120"></el-table-column>
              <el-table-column prop="taskNum" label="任务数" width="80"></el-table-column>
              <el-table-column label="diverted" width="80">
                <template #default="scope">
                  <el-tag v-if="scope.row.diverted" size="small">true</el-tag>
                  <el-tag v-else size="small" type="warning">false</el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="createException" label="创建异常" :show-overflow-tooltip="true">
                <template #default="scope">
                  <span v-if="scope.row.createException == null">无</span>
                  <span v-else>{{ scope.row.createException }}</span>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <!--表格-->
        <el-table-column prop="groupName" label="组名" width="80"></el-table-column>
        <el-table-column prop="taskId" label="任务ID" width="180" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="pluginId" label="插件ID" width="200" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="totalNumberOfPipline" label="管道数"></el-table-column>
        <el-table-column label="负载均衡">
          <template #default="scope">
            <el-tag v-if="scope.row.enableRebalance" size="small">true</el-tag>
            <el-tag v-else size="small" type="warning">false</el-tag>
          </template>
        </el-table-column>
      </el-table>
      <p>任务设置</p>
      <vue-json-editor v-model="detailSettingJsonConfig" :mode="'code'" lang="zh"></vue-json-editor>
      <p>任务行为</p>
      <vue-json-editor v-model="detailConfigJson" :mode="'code'" lang="zh"></vue-json-editor>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>

    <!--任务修改弹框-->
    <el-dialog title="任务修改" :visible.sync="dialogUpdateVisible" width="42%">
      <el-form :model="updateForm" label-position="left">
        <el-row>
          <el-col :span="11">
            <el-form-item label="任务ID" label-width="70px">
              <el-input v-model="updateForm.taskId" autocomplete="off" style="width: 250px;" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="管道数" label-width="70px" prop="pluginId">
              <el-input v-model="updateForm.totalNumberOfPipline" autocomplete="off" style="width: 250px;"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="任务名" label-width="70px" prop="pluginId">
              <el-input v-model="updateForm.taskName" autocomplete="off" style="width: 250px;"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="负载均衡" label-width="70px" prop="version">
              <el-select v-model="updateForm.enableRebalance" placeholder="请选择是否负载均衡" style="width: 250px">
                <el-option label=true value=true></el-option>
                <el-option label=false value=false></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <p>tags其它属性</p>
      <vue-json-editor v-model="updateForm.tags" :mode="'code'" lang="zh"></vue-json-editor>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="updateFormSubmit">确 定</el-button>
      </div>
    </el-dialog>

    <!--任务状态弹框-->
    <el-dialog title="任务状态" :visible.sync="dialogStateVisible" width="23%">
      <el-form :model="stateForm" label-position="left">
        <el-form-item label="任务ID" label-width="70px">
          <el-input v-model="stateForm.taskId" autocomplete="off" style="width: 250px;" disabled></el-input>
        </el-form-item>
        <el-form-item label="任务状态" label-width="70px" prop="version">
          <el-select v-model="stateForm.taskState" placeholder="请选择任务状态" style="width: 250px">
            <el-option v-for="(key, value) in states" :label="key" :value="value"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="updateStateFormSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import vueJsonEditor from 'vue-json-editor'

export default {
  inject: ['reload'],
  name: "Tasks",
  //父组件传值
  props: ['groupName'],
  mounted() {
    this.$nextTick(() => {
      this.$refs.main.scrollTop = this.$refs.content.scrollHeight;
    });

  },
  data() {
    return {
      //当前组任务
      taskList: [],
      //当前组插件
      pluginInstallList: [],
      //插件的类路径
      clazzList: [],
      //对话框
      dialogFormVisible: false,
      taskDetailVisible: false,
      dialogUpdateVisible: false,
      dialogStateVisible: false,
      //提交任务数据
      form: {
        groupName: this.groupName,
        pluginId: '',
        taskName: '',
        mydataharborCreatorClazz: '',
        totalNumberOfPipline: 1,
        enableRebalance: true,
        settingJsonConfig: {},
        configJson: {},
        tags: {}
      },
      //任务详情
      taskDetailForm: [],
      detailSettingJsonConfig: {},
      detailConfigJson: {},
      taskAssignedInfo: [],

      //状态
      taskState: {
        created: {
          zh: '创建',
          color: ''
        },
        started: {
          zh: '启动',
          color: 'success'
        },
        suspend: {
          zh: '暂停',
          color: 'info'
        },
        continued: {
          zh: '继续',
          color: 'warning'
        },
        over: {
          zh: '结束',
          color: 'over'
        }

      },
      states: {created: "创建", started: "启动", suspend: "暂停", continued: "继续", over: "结束"},
      //对话框标题
      dialogName: "提交任务",
      //修改数据
      updateForm: {
        enableRebalance: true,
        taskId: "",
        taskName: "",
        totalNumberOfPipline: 0,
        tags: {}
      },
      //修改状态
      stateForm: {
        taskId: "",
        taskState: ''
      },

      /*前端模拟分页*/
      // 所有页面的数据
      totalPage: [],
      // 每页显示数量
      pageSize: 10,
      // 共几页
      pageTotal: 1,
      // 当前显示的数据
      dataShow: [],
      // 默认当前显示第一页
      currentPage: 0,

      //多选内容
      multipleSelection: [],

      collapseActiveNames: ["settingConfig", "taskConfig", "tagConfig"],
      defaultActiveModel: "json",
      taskConfigDefaultActiveModel: "json",

      settingConfigJsonTreeData: [],

      taskConfigJsonTreeData: [],

      defaultProps: {
        children: 'fieldInfos',
        label: 'fieldName'
      }

    }
  },
  components: {
    vueJsonEditor
  },
  mounted() {
    this.initData();
    this.getTasksByGroupName();
  },
  watch: {
    "form.mydataharborCreatorClazz": function (val) {
      this.form.settingJsonConfig = {};
      this.form.configJson = {};
      if (val != "") {
        this.pluginInstallList.forEach((pluginInfo) => {
          if (pluginInfo.pluginId == this.form.pluginId) {
            pluginInfo.dataSinkCreatorInfos.forEach((creatorInfo) => {
              if (creatorInfo.clazz == val) {
                creatorInfo.settingClassInfo.fieldName = "root"
                creatorInfo.configClassInfo.fieldName = "root"
                this.settingConfigJsonTreeData = [creatorInfo.settingClassInfo];
                this.taskConfigJsonTreeData = [creatorInfo.configClassInfo];
                for (let fieldInfo of creatorInfo.settingClassInfo.fieldInfos) {
                  this.fillField(this.form.settingJsonConfig, fieldInfo);
                }
                for (let fieldInfo of creatorInfo.configClassInfo.fieldInfos) {
                  this.fillField(this.form.configJson, fieldInfo);
                }
              }
            });
          }
        });


      }
    }
  },
  methods: {

    //清空
    clearData() {
      this.form = {
        groupName: this.groupName,
        pluginId: '',
        mydataharborCreatorClazz: '',
        totalNumberOfPipline: 1,
        enableRebalance: true,
        settingJsonConfig: {},
        configJson: {}
      };
      this.taskDetailForm = [];
      this.dialogName = "提交任务";
    },
    initData() {
      //获取当前组的任务名称
      this.taskList = [];
      this.multipleSelection = [];

      //获取任务
      this.postRequest("mydataharbor/task/listTaskByGroup?groupName=" + this.groupName).then(res => {
        if (res.code == 0) {
          let allTask = res.data;

          for (let key in allTask) {
            this.taskList.push(allTask[key]);
          }
          //分页
          this.initPageData();
        }
      });
    },
    //根据组名查询任务
    getTasksByGroupName() {
      this.getRequest("/mydataharbor/node/plugin?groupName=" + this.groupName).then(res => {
        this.pluginInstallList = res.data;
        console.log(this.pluginInstallList);
      })
    },
    //提交任务
    submit() {
      this.dialogFormVisible = false;
      this.form.configJson = JSON.stringify(this.form.configJson);
      this.form.settingJsonConfig = JSON.stringify(this.form.settingJsonConfig);
      this.postRequest("mydataharbor/task/submit", this.form).then(res => {
        if (res.code == -1) {
          this.$message.error(res.msg);
        }

        if (res.code == 0) {
          this.$message.info("任务提交成功！");
          this.initData();
          this.clearData();
        }
      })
    },
    cancel() {
      this.dialogFormVisible = false;
      this.taskDetailVisible = false;
      this.dialogUpdateVisible = false;
      this.dialogStateVisible = false;
      this.clearData();
    },
    //选择插件
    selectPluginId(value) {
      this.clazzList = [];
      this.form.mydataharborCreatorClazz = "";
      this.pluginInstallList.forEach(plugin => {
        if (plugin.pluginId == value) {
          if (plugin.dataSinkCreatorInfos != null)
            plugin.dataSinkCreatorInfos.forEach(dataSink => {
              if (dataSink.canCreatePipline)
                this.clazzList.push({"clazz": dataSink.clazz, "type": dataSink.type});
            })
        }
      })
    },
    //任务详情
    taskDetail(task) {
      this.taskDetailForm = [];
      this.taskAssignedInfo = [];

      let row = JSON.parse(JSON.stringify(task));
      this.detailConfigJson = JSON.parse(task.configJson);
      this.detailSettingJsonConfig = JSON.parse(task.settingJsonConfig);
      //任务分配信息
      let assignedInfo = task.taskAssignedInfo.assignedInfoMap;
      for (let rowKey in assignedInfo) {
        this.taskAssignedInfo.push(assignedInfo[rowKey]);
      }
      //console.log(this.taskAssignedInfo);
      this.taskDetailForm.push(row);
      //console.log(this.taskDetailForm);
      this.taskDetailVisible = true;
    },
    //任务修改弹框
    taskUpdate(task) {
      this.updateForm.taskId = task.taskId;
      this.updateForm.taskName = task.taskName;
      this.updateForm.totalNumberOfPipline = task.totalNumberOfPipline;
      this.updateForm.enableRebalance = task.enableRebalance;
      this.updateForm.tags = task.tags;
      this.dialogUpdateVisible = true;
    },
    //任务修改
    updateFormSubmit() {
      this.postRequest("mydataharbor/task/editTask", this.updateForm).then(res => {
        if (res.code == 0) {
          this.$message.info("任务修改成功！");
          this.initData();
        }
      });
      this.dialogUpdateVisible = false;
    },
    //复制任务
    taskFork(task) {
      this.dialogName = "Fork任务";
      this.dialogFormVisible = true;
      this.form = JSON.parse(JSON.stringify(task));
      this.form.configJson = JSON.parse(task.configJson);
      this.form.settingJsonConfig = JSON.parse(task.settingJsonConfig);
    },
    //任务状态弹框
    taskStates(task) {
      this.stateForm.taskId = task.taskId;
      this.stateForm.taskState = task.taskState;
      this.dialogStateVisible = true;
    },
    //删除任务
    taskDelete(task) {
      this.$confirm('是否永久删除该任务：' + task.taskId, '确认信息', {
        distinguishCancelAndClose: true,
        confirmButtonText: '删除',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.postRequest("/mydataharbor/task/deleteTask?taskId=" + task.taskId).then(res => {
            if (res.code == 0) {
              this.$message({
                type: 'info',
                message: '删除成功！'
              });
            } else {
              this.$message({
                type: 'warning',
                message: '删除失败！' + res.msg
              });
            }
          })
          this.initData();
        });
    },
    updateStateFormSubmit() {
      this.postRequest("mydataharbor/task/manageTaskState?taskId=" + this.stateForm.taskId + "&taskState=" + this.stateForm.taskState).then(res => {
        if (res.code == 0) {
          this.$message.info("任务状态修改成功！");
          this.initData();
        } else {
          this.$message.error(res.msg);
        }
      });
      this.dialogStateVisible = false;
    },
    //分页处理
    initPageData() {
      this.pageTotal = this.taskList.length;
      for (let i = 0; i < this.pageTotal; i++) {
        // 每一页都是一个数组 形如 [['第一页的数据'],['第二页的数据'],['第三页数据']]
        // 根据每页显示数量 将后台的数据分割到 每一页,假设pageSize为5， 则第一页是1-5条，即slice(0,5)，第二页是6-10条，即slice(5,10)...
        this.totalPage[i + 1] = this.taskList.slice(this.pageSize * i, this.pageSize * (i + 1))
      }
      this.handlePageChange(1);
    },
    handlePageChange(pageNo) {
      this.currentPage = pageNo;
      // 获取到数据后显示第一页内容
      this.dataShow = this.totalPage[this.currentPage];
    },
    handleSizeChange(size) {
      this.pageSize = size;
      this.initPageData();
    },
    //多选
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    //删除多条数据
    batchDelete() {
      console.log(this.multipleSelection)
      if (this.multipleSelection.length == 0) {
        this.$message.info("当前未选择任务任务数据，请先勾选需要删除的数据！");
        return
      }
      let deleteCount = 0;
      this.$confirm('是否永久删除已选的' + this.multipleSelection.length + '条数据', '确认信息', {
        distinguishCancelAndClose: true,
        confirmButtonText: '删除',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.multipleSelection.forEach(task => {
            //console.log(task.taskId)
            this.postRequest("/mydataharbor/task/deleteTask?taskId=" + task.taskId).then(res => {
              if (res.code == 0) {
                deleteCount = deleteCount + 1;
                if (deleteCount == this.multipleSelection.length) {
                  this.$message({
                    type: 'info',
                    message: '成功删除' + deleteCount + '条数据！'
                  });
                }
              }
            })
          })
          this.reload();
        });
    },
    renderTreeContent(h, {node, data, store}) {

      return (
        <span class="custom-tree-node">
            <span>
              {data.fieldName} {data.title} 【类型:{data.clazzStr}】【{data["array"] == true ? "数组" : data['map'] == true ? "map" : data['baseType'] == true ? "基础类型" : "对象"}】
              {data['enumeration'] == true ? "【枚举】候选值:" + JSON.stringify(data['candidateValue']) : ""}
              {data["require"] == true ? "【必须】" : "【非必须】"}
              {data['defaultValue'] != null && data['defaultValue'] != "" ? "【默认值：" + data['defaultValue'] + "】" : ""}
              {data['des']}
            </span>


          </span>);
    },
    fillField(json, fieldInfo) {
      if (fieldInfo.require) {
        if (fieldInfo.array) {
          json[fieldInfo.fieldName] = [];
        } else if (fieldInfo.map) {
          json[fieldInfo.fieldName] = {};
        } else if (fieldInfo.baseType) {
          json[fieldInfo.fieldName] = fieldInfo.defaultValue;
        } else {
          json[fieldInfo.fieldName] = {};
        }
        if (fieldInfo.fieldInfos != null && fieldInfo.fieldInfos.length > 0) {
          for (let subFieldInfo of fieldInfo.fieldInfos) {
            this.fillField(json[fieldInfo.fieldName], subFieldInfo);
          }
        }
      }

    },
  }
}
</script>

<style scoped>
/deep/ .el-col-24 {
  width: 50%;
  margin-left: -6%;
}

/deep/ .el-alert--info.is-light {
  background-color: #ffffff;
}

.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

</style>



